export default {
	data() {
		return {
			swiperList: [
				{
					url: 'http://120.79.13.215/img/slide1.73e09e1d.jpg'
				},
				{
					url: 'http://120.79.13.215/img/slide2.0fbf4522.jpg'
				},
				{
					url: 'http://120.79.13.215/img/slide3.7a235263.jpg'
				}
			],
			// 列表
			list: {
				data: []
			}
		}
	},
	onLoad() {
		// uni.getSystemInfo({
		//     success: function (res) {
		// 			console.log(res)
		//     }
		// })
	},
	onShow () {
		if (uni.getStorageSync('no_regi')) {
			this.noRegi()
			return
		}
	},
	created () {
		// 获取列表
		this.getList()
	},
	methods: {
		// 获取列表
		getList () {
			this.$JRequest('caseList', data => {
				console.log(data)
				this.list.data = data.list
			})
		},
		// 未登记
		noRegi (msg = '未绑定或未登记') {
			this.$JFn.showModal({
				title: '提示',
				content: msg,
				cancelText: '去绑定',
				confirmText: '去登记',
				success (res) {
					if (res.confirm) {
						// 跳登记页面
						uni.navigateTo({
							url: '/pages/my/register/index'
						})
					} else if (res.cancel) {
						// 跳绑定页面
						uni.navigateTo({
							url: '/pages/my/bind/index'
						})
					}
				}
			})
		}
	}
}
